import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import Page from './components/Page';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Switch>
        <Route path="/">
          <Main />
        </Route>
      </Switch>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);

function Main() {
  return <Page />;
  
}
